<template lang="jade">
div#room-test
  .title
    .weui-panel
      .weui-panel__bd
        a.weui-media-box.weui-media-box_appmsg
          div.weui-media-box__hd
            img.weui-media-box__thumb(src="data:image/png;base64,")
          .weui-media-box__bd
            h4.weui-media-box__title 你画我猜 You draw I guess
            p.weui-media-box__desc 房间名称
  .player-waiting.weui-cells
    .waiting-wrap.weui-flex
      .player-item(v-for="i in playerNumber")
        .weui-btn.weui-btn_primary 玩家{{i}}
    .begin
      router-link.weui-btn.weui-btn_primary(:to="{ name: 'begin' }") 开始游戏
  chat
</template>
<!-- 进入房间后自动坐下，房主可开始游戏 -->
<script>
import Chat from './chat'
export default {
  components: { Chat },
  data () {
    return {
      playerNumber: 8
    }
  }
}
</script>

<style lang="less">
.player-waiting{
  padding:5vw 0;
  .waiting-wrap{
    text-align: center;
    flex-wrap:wrap;
  }
  .begin{
    padding: 5vw 5vw;
  }
}
.player-item{
  width: 25%;
  padding: 2vw;
}
</style>



